.EditFormContainer {
    width: 100%;
    height: 80%;
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    justify-items: center;
    align-items: center;
}

.EditForm {
    width: 503px;
    height: 379px;
    border-radius: 44px;
    background-color: #7ed321;
    display: flex;
    flex-direction: column;
    position: relative;
}

.saveForm {
    margin-top: 70px;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    justify-items: center;
}

.inputs {
    width: 320px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.inputElements {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 25px;
}

.inputTitle {
    margin-right: 15px;
    height: 19px;
    font-family: 'SFUIText', Arial, Helvetica, sans-serif;
    font-size: 16px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
}

.input {
    width: 236px;
    height: 31px;
    border-radius: 7px;
    border: solid 1px #979797;
    background-color: #ffffff;
}

.cancel {
    position: absolute;
    top: 27px;
    right: 27px;
    width: 27px;
    height: 27px;
}


.button {
    width: 320px;
    height: 42px;
    border-radius: 25px;
    border: solid 2px #ffffff;
    background-color: #7ed321;
    color: #ffffff;
}

.Save {
    font-family: 'SFUIText', Arial, Helvetica, sans-serif;
    width: 71px;
    height: 15px;
    font-size: 12px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
}

@media screen and (max-width: 420px) {
    .EditForm {
        width: 307px;
        height: 380px;
        border-radius: 25px;
    }

    .saveForm {
        margin-top: 45px;
    }

    .cancel {
        top: 21px;
        right: 21px;
        width: 18px;
        height: 19px;
    }

    .inputElements {
        display: flex;
        flex-direction: column;
        justify-content: center;
        margin-bottom: 20px;
    }

    .inputs {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .inputTitle {
        font-size: 13px;
    }

    .button {
        width: 237px;
    }
}
